/* eslint-disable no-console */
import Vue from 'vue'
import App from './App.vue'
import router from './router.js'
import store from './store'
import {i18n} from './i18n'

import 'bootstrap/dist/css/bootstrap.css'
import 'bootstrap-vue/dist/bootstrap-vue.css'
// import Buefy from 'buefy';
// import 'buefy/dist/buefy.css';


import {
  BootstrapVue,
  LayoutPlugin,
  ModalPlugin,
  CardPlugin,
  VBScrollspyPlugin,
  DropdownPlugin,
  TablePlugin,
  BreadcrumbPlugin,
  BootstrapVueIcons
} from 'bootstrap-vue'


Vue.config.productionTip = false


// Vue.use(Buefy);

Vue.use(BootstrapVue);
Vue.use(LayoutPlugin);
Vue.use(ModalPlugin);
Vue.use(CardPlugin);
Vue.use(VBScrollspyPlugin);
Vue.use(DropdownPlugin);
Vue.use(TablePlugin);
Vue.use(BreadcrumbPlugin);
Vue.use(BootstrapVueIcons);


const baseComponents = require.context('./components/base', false, /\.vue$/)
baseComponents.keys().forEach((fileName) => {
  const cmpCfg = baseComponents(fileName)
  const componentName = fileName.replace(/(^\.\/)|(\.\w+$)/g, '');
  if(!/^_/.test(componentName)) Vue.component(componentName, cmpCfg.default || cmpCfg)
});

export default new Vue({
  router,
  store,
  i18n,
  render: h => h(App)
}).$mount('#app');

store.dispatch('loadLang', store.getters.lang)
  .catch(e=>{
    console.log(`Ошибка при загрузке языка`);
    console.dir(e);
  });
store.dispatch('loadBuild')
  .catch(e=>{
    console.log(`Ошибка при загрузке информации о версии движка`);
    console.dir(e);
  });
