import Vue from '../main' 
import LS from '../helpers/localStorage'
import {i18n} from '../i18n'
import loadScript from '../helpers/scriptLoader'
// import DataShare from '../helpers/dataShare'

/** @param {TLang} lang */
export function loadLang({ commit, dispatch, getters }, lang) {
  // Если сохраненный язык не валиден, сбросить до дефолтного 
  if(!getters.langList.has(getters.lang)) lang = 'ru';

  fetch(`lang/${lang}.json`)
    .then(r=>r.json())
    .then(msgs => {
      i18n.setLocaleMessage(lang, msgs);
      i18n.locale = lang;
      document.querySelector('html').setAttribute('lang', lang);
      LS.save('s_l', lang);
      commit('set', {lang});
    })
    .catch(e=>{
      console.error('Не удалось загрузить файл локализации языка "'+lang+'": ', e)
    })
}
export function loadVersList({ commit, dispatch }) {
  return new Promise((suc, rej)=>{
    fetch(`e/build/list.json`)
      .then(r=>r.json())
      .then(vlist => {
        commit('set', {vlist});
        suc();
      })
      .catch(rej)
  })
}
export function changeVersion({ commit, dispatch }, v) {
  commit('sva', v);
  location.reload();
}


export function loadBuild({ commit, dispatch, getters }){
  return new Promise((suc, rej)=>{
    fetch('e/build/' + getters.sva + '.json')
      .then(r=>r.json())
      .then(build=>{
        commit('set', {build});
        suc();
      })
      .catch(rej)
  })
}


/** @param {TRunType} type */
export function loadEngine({ commit, dispatch, getters }, type){
  return new Promise((suc, rej)=>{
    
    loadScript(`e/core/${getters.v}/${type}.js`)
      .then(r=>{
        console.log(`Библиотеки загружены`);
        console.log(`Загрузка ядра: ${type}_${getters.v}.js`);
        suc('Процесс загрузки движка успешно завершен')
      })
      .catch(e=>rej(`Ошибка загрузки библиотек: ${e}`))
  })
}

