export function setSidebarHide({ commit }, isHide = true) {
  commit('set', {issbhidden: isHide});
}

/** @param {TVuexActionContext} ctx @param {TDialogOpts} opts */
export function dialog(ctx, opts) {
  ctx.commit('set', {modal: opts});
  Vue.$bvModal.show('modal_dialog');
}