
export default {
  from:{
    bool: val => !!+val,
    json(val, deflt){
      if(typeof val !== 'string')return deflt;
      try {
        return JSON.parse(val);
      } catch (e) {
        return deflt;
      }
    },
    time: {
      minutes: numer => new Date(numer * 60000),
    },
  },
  to:{
    bool: val => +val,
    json: JSON.stringify,
    time: {
      minutes: ms => Math.trunc(ms/60000),
    },
  },
  copy(obj){
    return JSON.parse(JSON.stringify(obj));
  }
}