import LS from '../helpers/localStorage';

export function set(state, data) {
  Object.assign(state, data);
}
export function sva(state, v) {
  LS.save('s_va', v.a || v.n); // Псевдоним или номер
  state.build.v = v.n
}

export function gameProjData(state, data) {
  state.sgproj = data;

  const rp = `p/${state.sgproj.devid}/${state.sgproj.projid}/`;
  window.$shared.R = {
    img: rp + 'i/',
    aud: rp + 'a/',
    shd: rp + 's/',
    map: rp + 'm/',
    loc: rp + 'l/',
  };
}

