const loadedScripts = new Set();
/**
 * @param {String} url - Url загружаемого скрипта
 * @returns {Promise}
 */
export default function(url, isSkipOnHasAlready = false) {
  return new Promise((success, error)=>{
    if(isSkipOnHasAlready && loadedScripts.has(url)){
      // Если на странице уже есть такой скрипт
      return success();
    }
    const el = document.createElement('script');
    el.src = url;
    el.onload = () => {
      loadedScripts.add(url);
      success();
    }
    el.onerror = () => {
      error()
    }
    document.head.append(el);
  })
}