<template>
  <div class="img_pixel" :style="url ? `background: url(${url})` : `background: #${color || '0000'}`">
    <img
      style="height:100%;width:100%;visibility:hidden"
      :src="url"
      @error="!src.startsWith('data:') ? url=fail : 0"
    />
  </div>
</template>

<script>
export default {
  props: ['src', 'fail', 'color'],
  data(){
    return { url: this.src }
  },
  watch: {
    src(v){ this.url = v}
  }
}
</script>

<style>
  .img_pixel{
    background-repeat: no-repeat !important;
    background-size: contain !important;
    image-rendering: pixelated !important;
  }
</style>