/* eslint-disable no-prototype-builtins */

const watchList = new Map();
window.addEventListener('storage', (e)=>{  
  const targetCb = watchList.get(e.key);
  if(targetCb){
    targetCb(e.newValue , e);
  }
});

function save(key, val) {
  window.localStorage.setItem(key, val);
}
function load(key, defaultVal) {
  const val = window.localStorage.getItem(key);
  return val != null ? val : defaultVal;
}


const separator = '#'
function getKeyListByPrefix(prefix){
  const ixList = [];
  const regexp = new RegExp('^'+prefix+separator+'\\d+$');
  for( let key in localStorage ){
    if(localStorage.hasOwnProperty(key) && regexp.test(key) ){
      ixList.push(key.match(/\d+$/)[0])
    }
  }
  ixList.sort((a,b)=>a-b); 
  return ixList.map(i=>prefix+separator+i);
}


export default {
  watch: (key, cb) =>watchList.set(key, cb),
  save(key, val, converter){
    save(key, converter ? converter(val) : val);
  },
  load(key, dflt, converter){
    const data = load(key, dflt);
    return converter ? converter(data) : data;
  },
  many: {
    getNextKey(prefix){
      // let nextId = (+load('ai_'+prefix)) || 0;
      let nextId = 0;
      while (load(prefix+separator+nextId)) { ++nextId } // Поиск свободного индекса
      // save('ai_'+prefix, nextId+1);
      return prefix+separator+nextId;
    },
    // add(prefix, val, converter){
    //   save(prefix+separator+nextId, converter ? converter(val) : val);
    // },
    loadAll(prefix, converter){
      const targetList = getKeyListByPrefix(prefix);
      const res = new Map();
      targetList.forEach(key => {
        const data = load(key);
        const convdData = converter ? converter(data) : data;
        if(convdData != null && !(typeof convdData === 'number' && isNaN(convdData))){
          res.set(key, convdData)
        }
      });
      return res;
    },
    loadKeys: getKeyListByPrefix,
  }
}