import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  { path: '*', redirect: '/game' },
  {
    path: '/game',
    component: () => import(/* webpackChunkName: "game" */ './pages/Game.vue'),
  },
  {
    path: '/editor',
    component: () => import(/* webpackChunkName: "editor" */ './pages/Editor.vue'),
  },
  {
    path: '/wiki',
    component: () => import(/* webpackChunkName: "news" */ './pages/Wiki.vue'),
  },
  {
    path: '/news',
    component: () => import(/* webpackChunkName: "news" */ './pages/News.vue'),
  },
  {
    path: '/settings',
    component: () => import(/* webpackChunkName: "settings" */ './pages/Settings.vue'),
  },
  {
    path: '/about',
    component: () => import(/* webpackChunkName: "about" */ './pages/About.vue'),
  }
];

export default new VueRouter({
  base: process.env.BASE_URL,
  routes,
  ...(process.env.NODE_ENV === 'production' ? {
    mode: 'history'
  } : {}),
})
